import {
  MutableRefObject,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import {
  NativeScrollEvent,
  NativeSyntheticEvent,
  ScrollView,
} from "react-native";
import {
  runOnJS,
  SharedValue,
  useAnimatedReaction,
  useSharedValue,
} from "react-native-reanimated";
import { interactManager, useAsyncFn, useAutoToastError } from "@true/core";
import { requestBookings } from "@/store/booking/function";
import { Event } from "@/screens/CalendarScreen/type";
import {
  getBookings,
  getBookingsByQuery,
  useBookingsByQuery,
} from "@/store/booking";
import moment from "moment";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";
import { getDSEndOfTFromDS, getDSStartOfTFromDS } from "@/utils/calendar";

export const useTimelineOffset = (props: {
  onChangeOffset?: (offset: number) => void;
  scrollOffset?: number;
  scrollViewRef: MutableRefObject<ScrollView | null>;
}) => {
  const { onChangeOffset, scrollOffset, scrollViewRef } = props;
  useEffect(() => {
    // NOTE: The main reason for this feature is to sync the offset
    // between all of the timelines in the TimelineList component
    if (scrollOffset !== undefined) {
      scrollViewRef?.current?.scrollTo({
        y: scrollOffset,
        animated: false,
      });
    }
  }, [scrollOffset]);
  const onScrollEndDrag = useCallback(
    (event: NativeSyntheticEvent<NativeScrollEvent>) => {
      const offset = event.nativeEvent.contentOffset.y;
      const velocity = event.nativeEvent.velocity?.y;
      if (velocity === 0) {
        onChangeOffset?.(offset);
      }
    },
    []
  );
  const onMomentumScrollEnd = useCallback(
    (event: NativeSyntheticEvent<NativeScrollEvent>) => {
      onChangeOffset?.(event.nativeEvent.contentOffset.y);
    },
    []
  );
  return {
    scrollEvents: {
      onScrollEndDrag,
      onMomentumScrollEnd,
    },
  };
};

export const useSelectedItems = (
  dateString: SharedValue<string>,
  selectedType: SharedValue<string>
) => {
  const [selectedDate, setDate] = useState(dateString.value);
  const [type, setType] = useState(selectedType.value);

  useAnimatedReaction(
    () => dateString.value,
    (cur) => runOnJS(setDate)(cur),
    [setDate]
  );

  const onSetType = useCallback(
    (_type: string) => {
      interactManager(() => setType(_type), 300);
    },
    [setType]
  );

  useAnimatedReaction(
    () => selectedType.value,
    (cur) => runOnJS(onSetType)(cur),
    [onSetType]
  );

  return {
    selectedDate,
    type,
  };
};

export const useRequestBookings = ({
  index,
  query,
  tab,
  params,
  mainKey = "filter",
}: {
  index: number;
  tab?: string;
  query?: string;
  params?: any;
  mainKey?: string;
}) => {
  const [events, setEvents] = useState<Event[]>([]);
  const isFirstLoad = useSharedValue(true);
  const isAction = useSharedValue(false);
  const { selectedDateString, currentTabIndex, showCalendar, selectedStatus } =
    useBookingProvider();

  /**
   * get query to set in redux
   */
  const _query = useMemo(
    () => (query || "") + (query && tab ? "/" : "") + (tab || "") || "all",
    [query, tab]
  );

  const getEvents: any = useCallback(
    (ids?: any) => {
      const _ids = ids || getBookingsByQuery(_query);
      return [...(_ids || [])]
        .map((id) => {
          const _booking = getBookings(id) as any;
          if (
            selectedDateString.value ===
            moment(_booking?.stime, "X").format("YYYY-MM-DD")
          ) {
            return {
              id,
              start: moment(_booking?.stime, "X").format("YYYY-MM-DD HH:mm:ss"),
              end: moment(_booking?.etime, "X").format("YYYY-MM-DD HH:mm:ss"),
              title: _booking?.name || "",
            };
          }
        })
        .filter(Boolean);
    },
    [_query, selectedDateString]
  );

  const _itemList = useBookingsByQuery(_query);
  useEffect(() => {
    if (!isAction.value && !isFirstLoad.value && !showCalendar.value) {
      setEvents(getEvents(_itemList));
    }
  }, [_itemList]);

  /**
   * request function
   */
  const [{ error: firstError }, onFirstLoad] = useAsyncFn(requestBookings, []);
  const [{ error: changeTabError }, onChangeTab] = useAsyncFn(
    requestBookings,
    []
  );
  const [{ loading: refreshing, error: refreshError }, onRefresh] = useAsyncFn(
    requestBookings,
    []
  );

  useAutoToastError(firstError);
  useAutoToastError(changeTabError);
  useAutoToastError(refreshError);

  /**
   * get params before submit
   */
  const submitParams = useMemo(() => {
    let result: any = {};

    if (params) {
      for (let key in params) {
        if (key !== mainKey && params[key]) result[key] = params[key];
      }
    }

    if (tab) result[mainKey] = tab;

    return result;
  }, [params, mainKey, tab]);

  /**
   * get prev params to determinded when params change
   */
  const prevParams = useSharedValue(submitParams);

  /**
   * onRefresh
   */
  const onLoadData = useCallback(() => {
    // isAction.value = true;
    onRefresh({
      ...submitParams,
      start: getDSStartOfTFromDS("month", selectedDateString.value),
      end: getDSEndOfTFromDS("month", selectedDateString.value),
      status: selectedStatus.value,
    }).then(() => {
      setEvents(getEvents() as any);
      // isAction.value = false;
    });
    return;
  }, [submitParams, onRefresh, _query, events.length]);

  /**
   * First load
   */
  const _firstLoad = useCallback(() => {
    interactManager(() => {
      onFirstLoad({
        ...submitParams,
        start: getDSStartOfTFromDS("month", selectedDateString.value),
        end: getDSEndOfTFromDS("month", selectedDateString.value),
        status: selectedStatus.value,
      }).then(() => {
        setEvents(getEvents() as any);
        // onDataChange?.(_items);
        isFirstLoad.value = false;
        isAction.value = false;
        // setMounted(true);
      });
    }, 300);
  }, [_query, getEvents]);

  useAnimatedReaction(
    () => currentTabIndex.value,
    (cur) => {
      if (index == cur && isFirstLoad.value) {
        // console.log("first Load");
        isAction.value = true;
        runOnJS(_firstLoad)();
      }
    },
    [index, _firstLoad]
  );

  /**
   * Change Tab Load
   */
  const _refresh = useCallback(
    (timeout: number, isClear?: boolean) => {
      // console.log("change Tab Load");
      isAction.value = true;
      // if (!isClear) setChangeParamsLoading(true);
      interactManager(
        () => {
          onChangeTab({
            ...submitParams,
            start: getDSStartOfTFromDS("month", selectedDateString.value),
            end: getDSEndOfTFromDS("month", selectedDateString.value),
            status: selectedStatus.value,
          }).then(() => {
            setEvents(getEvents());
            // onDataChange?.(_items);
            isAction.value = false;
            // setChangeParamsLoading(false);
          });
        },
        timeout,
        isClear
      );
    },
    [tab, submitParams, _query, selectedDateString]
  );

  useAnimatedReaction(
    () => ({
      _index: currentTabIndex.value,
      _prevParams: prevParams.value,
      _selectedDateString: selectedDateString.value,
      _selectedStatus: selectedStatus.value,
    }),
    ({ _index, _prevParams, _selectedDateString, _selectedStatus }, prev) => {
      // only fetch after first load
      if (!isFirstLoad.value) {
        // fetch when on current tab
        if (_index == index) {
          // fetch when change params
          if (
            JSON.stringify(submitParams) !== JSON.stringify(_prevParams) ||
            prev?._selectedDateString !== _selectedDateString ||
            prev?._selectedStatus !== _selectedStatus
          ) {
            // console.log("change params");
            // page.value = 1;
            // isLoadMorable.value = true;
            prevParams.value = submitParams;
            runOnJS(_refresh)(1000);
          }
          // fetch when change tab
          else if (_index !== prev?._index) {
            // console.log("change tab");
            runOnJS(_refresh)(2000, true);
          }
        }
      }
    },
    [_refresh, submitParams, index]
  );

  // const onSetEvents = useCallback(() => {
  //   isAction.value = true;
  //   interactManager(() => {
  //     setEvents(getEvents());
  //     isAction.value = false;
  //   }, 1000);
  // }, []);
  //
  // useAnimatedReaction(
  //   () => ({
  //     _selectedDateString: selectedDateString.value,
  //     _index: currentTabIndex.value,
  //   }),
  //   (cur, prev) => {
  //     if (
  //       !isFirstLoad.value &&
  //       cur._index === index &&
  //       cur._selectedDateString !== prev?._selectedDateString
  //     ) {
  //       runOnJS(onSetEvents)();
  //     }
  //   },
  //   [onSetEvents, index]
  // );

  return useMemo(
    () => ({ events, isAction, onLoadData, refreshing }),
    [events, isAction, onLoadData, refreshing]
  );
};
