import React, { memo } from "react";
import {
  DrawerStack,
  DrawStackComponent,
  MainStack,
  MainStackComponent,
  ModalStack,
  ModalStackComponent,
  TabBarIcon,
  TabBarStack,
  TabBarStackComponent,
} from "@true/ui";
import ListAppScreen from "@multiple-platform/applist";
import { lazily, translate } from "@true/core";
import { View } from "react-native";
import { Icons } from "@true/asset";
import { CalendarScreen } from "@/screens/CalendarScreen";
import { FloatingButtonTabBar } from "@/components/FloatingButtonTabBar";
import { CreateBookingScreen } from "@/screens/CreateBookingScreen";
import { BookingDetailScreen } from "@/screens/BookingDetailScreen";
import { SelectResourceScreen } from "@/screens/CreateBookingScreen/components/SelectResourceScreen";
import { ResourcesScreen } from "@/screens/ResoucesScreen";
import { ResourceListScreen } from "@/screens/ResoucesScreen/ResourceListScreen";
import { ResourceDetailScreen } from "@/screens/ResourceDetailScreen";

const { HomeScreen } = lazily(() => import("@/screens/HomeSceen"));
const { FilterBookingScreen } = lazily(
  () => import("@/screens/CalendarScreen/FilterBookingScreen")
);

const Tab = memo(() => {
  return (
    <TabBarStackComponent centerElement={<FloatingButtonTabBar />}>
      <TabBarStack.Screen
        name={"Calendar"}
        component={CalendarScreen}
        options={{
          title: translate("booking.calendar"),
          tabBarIcon: ({ focused }) => (
            <TabBarIcon
              isFocused={focused}
              icon={focused ? Icons.Filled.HOME : Icons.Outlined.HOME}
            />
          ),
        }}
      />
      <TabBarStack.Screen
        name={"Resources"}
        component={ResourcesScreen}
        options={{
          title: translate("booking.resources"),
          tabBarIcon: ({ focused }) => (
            <TabBarIcon
              isFocused={focused}
              icon={focused ? Icons.Filled.CUBE : Icons.Outlined.CUBE}
            />
          ),
        }}
      />
      <TabBarStack.Screen
        name={" "}
        component={() => null}
        options={{
          title: "",
          tabBarIcon: ({ focused }) => null,
          // @ts-ignore
          disabled: true,
        }}
      />
      <TabBarStack.Screen
        name={"Platform"}
        component={ListAppScreen}
        options={{
          title: translate("booking.platform"),
          tabBarIcon: ({ focused }) => (
            <TabBarIcon
              isFocused={focused}
              icon={focused ? Icons.Filled.SQUARE_2X2 : Icons.Outlined.SQUARE_2X2}
            />
          ),
        }}
      />
      <TabBarStack.Screen
        name={"Menu"}
        component={HomeScreen}
        options={{
          title: translate("booking.menu"),
          tabBarIcon: ({ focused }) => (
            <TabBarIcon
              isFocused={focused}
              icon={focused ? Icons.Filled.BARS_3 : Icons.Outlined.BARS_3}
            />
          ),
        }}
      />
    </TabBarStackComponent>
  );
});

const Drawer = memo(() => {
  return (
    <DrawStackComponent drawerContent={(props) => <View />}>
      <DrawerStack.Screen name={"Main"} component={Tab} />
    </DrawStackComponent>
  );
});

const Main = memo(() => {
  return (
    <MainStackComponent>
      <MainStack.Screen name={"Main"} component={Drawer} />
      <MainStack.Screen
        name={"CreateBookingScreen"}
        component={CreateBookingScreen}
      />
      <MainStack.Screen
        name={"ResourceListScreen"}
        component={ResourceListScreen}
      />
      <MainStack.Screen
        name={"ResourceDetailScreen"}
        component={ResourceDetailScreen}
      />
      <MainStack.Screen
        name={"BookingDetailScreen"}
        component={BookingDetailScreen}
      />
    </MainStackComponent>
  );
});

export const Modal = memo(() => {
  return (
    <ModalStackComponent>
      <ModalStack.Screen name={"Main"} component={Main} />
      <ModalStack.Screen
        name={"SelectResourceScreen"}
        component={SelectResourceScreen}
      />
      <ModalStack.Screen
        name={"FilterBookingScreen"}
        component={FilterBookingScreen}
      />
    </ModalStackComponent>
  );
});
