import { memo } from "react";
import { Event } from "../../../type";
import { styled } from "@true/core";
import { BookingCard } from "@/screens/CalendarScreen/components/calendar/ListBookingsCard/BookingCard";

interface Props {
  events: Event[];
}

export const ListBookingsCard = memo(({ events }: Props) => {
  return (
    <SWrapper>
      {events && events.length
        ? events.map((event, index) => (
            <BookingCard key={event.id || index} id={event.id} />
          ))
        : null}
    </SWrapper>
  );
});

const SWrapper = styled.View`
  background-color: ${(p) => p.theme.backgroundColor};
  flex: 1;
  padding: 12px 16px;
`;
