import React, { memo, useEffect } from "react";
import { styled, translate } from "@true/core";
import {
  DynamicHeader,
  LeftModalHeader,
  ScreenWrapper,
  ScrollableTabView,
  UIButton,
} from "@true/ui";
import { Icons } from "@true/asset";
import { ListBookings } from "@/screens/CalendarScreen/components/ListBookings";
import { useSharedValue, useWorkletCallback } from "react-native-reanimated";
import { requestResoures } from "@/store/resources/function";
import { FilterBooking } from "@/screens/CalendarScreen/components/FilterBooking";
import {
  BookingProvider,
  useBookingProvider,
} from "@/screens/CalendarScreen/provider";
import { MyCalendar } from "@/screens/CalendarScreen/components/calendar/MyCalendar";
import { SelectTypeBooking } from "@/screens/CalendarScreen/components/SelectTypeBooking";
import { requestResourceGroups } from "@/store/groups/function";
import { SelectStatusBooking } from "@/screens/CalendarScreen/components/SelectStatusBooking";

export const _CalendarScreen = memo(() => {
  const scrollEnabled = useSharedValue(false);
  const { onLayoutScrollableTab, currentTabIndex } = useBookingProvider();
  // console.log(Core.clientKey, "cdacadcdacdacadc", Core.accessToken);

  useEffect(() => {
    requestResoures({}).then();
    requestResourceGroups().then();
  }, []);

  const onChangeTab = useWorkletCallback(
    (index: number) => (currentTabIndex.value = index)
  );

  return (
    <SScreenWrapper>
      <DynamicHeader
        title={translate("booking.calendar")}
        rightComponent={<SUIButton source={Icons.Filled.MAGNIFYING_GLASS} />}
        hideSeparator={true}
      />

      <ScrollableTabView.Provider
        scrollEnabled={scrollEnabled}
        loadMoreParams={(data, page) => ({ p: page })}
        stickyHeaderRender={<FilterBooking />}
        onChangeTab={onChangeTab}
        tabs={[
          {
            title: translate("booking.following"),
            content: (p) => <ListBookings index={p.index} tab={"following"} />,
          },
          {
            title: translate("booking.create_by_me"),
            content: (p) => <ListBookings index={p.index} tab={"created"} />,
          },
          {
            title: translate("booking.assigned_to_me"),
            content: (p) => <ListBookings index={p.index} tab={"tome"} />,
          },
          {
            title: translate("booking.all_bookings"),
            content: (p) => <ListBookings index={p.index} tab={"all"} />,
          },
        ]}
        // ListEmptyComponent={renderEmptyView}
      >
        <ScrollableTabView.TabBar onLayout={onLayoutScrollableTab} />
        <MyCalendar />
        <SelectTypeBooking />
        <SelectStatusBooking />
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    </SScreenWrapper>
  );
});

export const CalendarScreen = memo(() => {
  return (
    <BookingProvider>
      <_CalendarScreen />
    </BookingProvider>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.neutral8};
`;

const SUIButton = styled(UIButton)`
  margin-right: 16px;
`;

const SBackBtn = styled(UIButton).attrs((p) => ({
  source: Icons.Outlined.CHEVRON_LEFT,
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral1,
  },
}))`
  width: 24px;
  height: 24px;
`;
