import React from "react";
import { ScrollView } from "react-native";
import { memoForwardRef, styled, translate } from "@true/core";
import {
  AnimatedBottomSpace,
  LeftModalHeader,
  ScreenWrapper,
  UIFlashList,
} from "@true/ui";
import { useResourceGroupsByQuery } from "@/store/groups";
import { ResourceGroupItem } from "@/screens/ResoucesScreen/components/ResourceGroupItem";

export const ResourcesScreen = memoForwardRef(() => {
  const groupIds = useResourceGroupsByQuery("all");

  return (
    <SScreenWrapper>
      <LeftModalHeader
        title={translate("booking.resource_groups")}
        hideCloseIcon={true}
      />
      <SScrollView>
        <UIFlashList
          data={groupIds}
          numColumns={2}
          renderItem={({ item, index }) => <ResourceGroupItem id={item} />}
        />
        <AnimatedBottomSpace />
      </SScrollView>
    </SScreenWrapper>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.neutral8};
`;

const SScrollView = styled(ScrollView)`
  padding-left: 16px;
`;
