import { createArrayReducer, setupStore } from "@true/core";
import { bookingsReducer, setBookingsStore } from "@/store/booking";
import { resourcesReducer, setResourcesStore } from "@/store/resources";
import { resourceGroupsReducer, setResourceGroupsStore } from "@/store/groups";

export const { reducer: okReducer, setStore: setOkStore } = createArrayReducer(
  "ok",
  ["id"]
);

/* -- add your reducers in here (*required)-- */
export const reducers = {
  bookings: bookingsReducer,
  resources: resourcesReducer,
  groups: resourceGroupsReducer,
};

/* -- setup store -- */
export const { persistor } = setupStore(reducers, (store) => {
  setBookingsStore(store);
  setResourcesStore(store);
  setResourceGroupsStore(store);
});
