import inRange from "lodash/inRange";
import XDate from "xdate";
import { constants } from "./utils";
import { Event } from "react-native-calendars/src/timeline/EventBlock";
import { PopulateOptions } from "@/screens/CalendarScreen/type";
import moment from "moment";
export const HOUR_BLOCK_HEIGHT = 100;
const OVERLAP_EVENTS_SPACINGS = 10;
const RIGHT_EDGE_SPACING = 10;
function buildEvent(
  event: any,
  left: number,
  width: number,
  { dayStart = 0, hourBlockHeight = HOUR_BLOCK_HEIGHT }
) {
  const startTime = new XDate(event.start);
  const endTime = event.end
    ? new XDate(event.end)
    : new XDate(startTime).addHours(1);
  const dayStartTime = new XDate(startTime).clearTime();
  return {
    ...event,
    top: (dayStartTime.diffHours(startTime) - dayStart) * hourBlockHeight,
    height: startTime.diffHours(endTime) * hourBlockHeight,
    width,
    left,
  };
}
function hasCollision(a: any, b: any) {
  return a.end > b.start && a.start < b.end;
}
function calcColumnSpan(event: Event, columnIndex: number, columns: any[]) {
  let colSpan = 1;
  for (let i = columnIndex + 1; i < columns.length; i++) {
    const column = columns[i];
    const foundCollision = column.find((ev: any) => hasCollision(event, ev));
    if (foundCollision) {
      return colSpan;
    }
    colSpan++;
  }
  return colSpan;
}

function packOverlappingEventGroup(
  columns: any[],
  calculatedEvents: any[],
  populateOptions: PopulateOptions
) {
  const {
    screenWidth = constants.screenWidth,
    rightEdgeSpacing = RIGHT_EDGE_SPACING,
    overlapEventsSpacing = OVERLAP_EVENTS_SPACINGS,
  } = populateOptions;
  const arr: any[] = [];
  columns.forEach((column, columnIndex) => {
    column.forEach((event: any) => {
      const totalWidth = screenWidth - rightEdgeSpacing;
      const columnSpan = calcColumnSpan(event, columnIndex, columns);
      const percentWidth = columnSpan === columns.length ? 1 : 0.8;
      // const eventLeft = (columnIndex / columns.length) * totalWidth;
      const eventLeft = columnIndex * totalWidth * percentWidth;
      // let eventWidth = totalWidth * (columnSpan / columns.length);
      let eventWidth = totalWidth * percentWidth;
      if (columnIndex + columnSpan <= columns.length - 1) {
        eventWidth -= overlapEventsSpacing;
      }
      calculatedEvents.push(
        buildEvent(event, eventLeft, eventWidth, populateOptions)
      );
    });
  });
}

export function populateEvents(
  _events: any[],
  populateOptions: PopulateOptions
) {
  let lastEnd: any = null;
  let columns: any[] = [];
  const calculatedEvents: any[] = [];
  const calculatedEventGroup: any[][] = [];
  const events = _events
    .map((ev, index) => ({ ...ev, index: index }))
    .sort(function (a, b) {
      if (a.start < b.start) return -1;
      if (a.start > b.start) return 1;
      if (a.end < b.end) return -1;
      if (a.end > b.end) return 1;
      return 0;
    });
  events.forEach(function (ev) {
    // Reset recent overlapping event group and start a new one
    if (lastEnd !== null && ev.start >= lastEnd) {
      packOverlappingEventGroup(columns, calculatedEvents, populateOptions);
      columns = [];
      lastEnd = null;
    }
    // Place current event in the right column where it doesn't overlap
    let placed = false;
    for (let i = 0; i < columns.length; i++) {
      const col = columns[i];
      if (!hasCollision(col[col.length - 1], ev)) {
        col.push(ev);
        placed = true;
        break;
      }
    }
    // If curr event wasn't placed in any of the columns, create a new column for it
    if (!placed) {
      columns.push([ev]);
    }
    if (lastEnd === null || ev.end > lastEnd) {
      lastEnd = ev.end;
    }
  });
  if (columns.length > 0) {
    packOverlappingEventGroup(columns, calculatedEvents, populateOptions);
  }

  let index = 0;

  calculatedEvents.forEach((ev, i) => {
    if (!calculatedEventGroup[index]) {
      calculatedEventGroup[index] = [ev];
    } else {
      const findItem = calculatedEventGroup[index].find(
        (_ev) => _ev?.id === ev?.id
      );

      if (!findItem) {
        index++;
        calculatedEventGroup[index] = [ev];
      }
    }

    for (let j = i + 1; j < calculatedEvents.length; j++) {
      if (hasCollision(events[j], events[i])) {
        calculatedEventGroup[index].push(calculatedEvents[j]);
      } else {
        index += calculatedEventGroup[index].length > 1 ? 0 : 1;
        break;
      }
    }
  });

  return {
    calculatedEvents,
    calculatedEventGroup: calculatedEventGroup.map((groups) => [
      ...new Set(groups),
    ]),
  };
}
