import constate from "constate";
import { useMemo } from "react";
import { useSharedValue, useWorkletCallback } from "react-native-reanimated";
import { LayoutChangeEvent } from "react-native";
import moment from "moment";
import { EFilterType } from "@/screens/CalendarScreen/type";

const _provider = () => {
  const heightScrollableTab = useSharedValue(0);
  const showCalendar = useSharedValue(0);
  const selectedDateString = useSharedValue(moment().format("YYYY-MM-DD"));
  const currentTabIndex = useSharedValue(0);

  // dropdown to display select booking type
  const selectedType = useSharedValue("schedule");
  const showSelectType = useSharedValue(0);
  const positionSelectType = {
    top: useSharedValue(0),
    left: useSharedValue(0),
  };

  // dropdown to display select status booking
  const selectedStatus = useSharedValue("");
  const showSelectStatus = useSharedValue(0);

  const onShowFilterType = useWorkletCallback((type: EFilterType) => {
    switch (type) {
      case EFilterType.CALENDAR:
        showCalendar.value = showCalendar.value ? 0 : 1;
        showSelectType.value = 0;
        showSelectStatus.value = 0;
        break;
      case EFilterType.BOOKING_TYPE:
        showCalendar.value = 0;
        showSelectType.value = showSelectType.value ? 0 : 1;
        showSelectStatus.value = 0;
        break;
      case EFilterType.BOOKING_STATUS:
        showCalendar.value = 0;
        showSelectType.value = 0;
        showSelectStatus.value = showSelectStatus.value ? 0 : 1;
        break;
    }
  });

  const onLayoutScrollableTab = useWorkletCallback(
    ({
      nativeEvent: {
        layout: { height },
      },
    }: LayoutChangeEvent) => {
      heightScrollableTab.value = height;
    },
    []
  );
  return useMemo(
    () => ({
      onLayoutScrollableTab,
      heightScrollableTab,
      showCalendar,
      selectedDateString,
      currentTabIndex,
      selectedType,
      showSelectType,
      positionSelectType,
      selectedStatus,
      showSelectStatus,
      onShowFilterType,
    }),
    [
      onLayoutScrollableTab,
      heightScrollableTab,
      showCalendar,
      selectedDateString,
      currentTabIndex,
      selectedType,
      showSelectType,
      positionSelectType,
      selectedStatus,
      showSelectStatus,
      onShowFilterType,
    ]
  );
};

export const [BookingProvider, useBookingProvider] = constate(
  _provider,
  (val) => val
);
