import { Core, Fetch } from "@true/core";
import { IBooking } from "@/store/booking/type";
import { IResourceGroups } from "@/store/groups/type";
import { batch } from "react-redux";
import {
  setResourceGroupsQuery,
  syncResourceGroups,
} from "@/store/groups/index";

export const requestResourceGroups = async () => {
  const { data } = await Fetch.postWithToken<{
    groups: IResourceGroups[];
  }>("@true/booking/ajax/api/mobile/groups");

  batch(() => {
    if (data.groups && data.groups.length) {
      syncResourceGroups(data.groups);
      setResourceGroupsQuery((prev) => ({
        ...prev,
        all: data.groups.map((group) => group.id.toString()),
      }));
    }
  });
};
