import React, { memo } from "react";
import { addLangResources, AppInfo, useNavigationParams } from "@true/core";
import { MyMiniApp } from "@true/ui";
import { Modal } from "@/Routes";
import { persistor } from "@/store";

/* -- setup app info -- */

AppInfo.title = "Booking";
AppInfo.subTitle = "Manage all resource booking";
AppInfo.primaryColor = "#3AA394";
AppInfo.buttonTextColor = "#fff";
AppInfo.logo = require("@/assets/images/ic_calendar_event.png");
AppInfo.preloadImage = require("@/assets/images/ic_calendar_event.png");
AppInfo.preSyncData = async () => {
  // your pre request function
};

/* -- add languages -- */
addLangResources({
  en: { ...require("@/assets/languages/en.json") },
  vi: { ...require("@/assets/languages/vi.json") },
}).then();

const MiniApp = memo(() => {
  const props = useNavigationParams<any>();
  return (
    <MyMiniApp
      component={Modal}
      persistor={persistor}
      onBeforeLift={props?.callback}
    />
  );
});

export default MiniApp;
