import React, { memo } from "react";
import { styled, translate, useUser } from "@true/core";
import { Avatar, toBoolean, UIText } from "@true/ui";
import { useResources } from "@/store/resources";
import { AvatarList } from "@/components/Avatars/AvatarList";
import { EWorkflow } from "@/store/resources/type";

interface Props {
  id: string | number;
}

export const getTextWorkflow = (workflow?: string) => {
  const data = {
    [EWorkflow.AUTO_APPROVE]: translate("booking.automatically_approve"),
    [EWorkflow.ONE_APPROVE]: translate("booking.one_approve"),
    [EWorkflow.PARALLEL]: translate("booking.parallel"),
    [EWorkflow.SEQUENTIAL]: translate("booking.sequential"),
  };

  // @ts-ignore
  return data[workflow] || translate("booking.automatically_approve");
};

export const ReservationManagement = memo(({ id }: Props) => {
  const resource = useResources(id);
  const manager = useUser(resource?.assign_to);
  return (
    <SWrapper>
      <SRow>
        <STitle>{translate("booking.manager")}:</STitle>
        <SAvatarName>
          <Avatar userKey={manager?.id || 0} size={24} />
          <SText>{manager?.name}</SText>
        </SAvatarName>
      </SRow>
      {resource?.followers && resource?.followers.length > 0 ? (
        <SRow>
          <STitle>{translate("booking.followers")}:</STitle>
          <AvatarList userIds={resource?.followers} size={24} maxUsers={3} />
        </SRow>
      ) : null}
      {resource?.owners && resource?.owners.length > 0 ? (
        <SRow>
          <STitle>{translate("booking.approvers")}:</STitle>
          {resource?.owners.length > 1 ? (
            <AvatarList userIds={resource?.owners} size={24} maxUsers={3} />
          ) : (
            <SAvatarName>
              <Avatar userKey={resource?.owners[0] || 0} size={24} />
              <SText>{useUser(resource?.owners[0])?.name}</SText>
            </SAvatarName>
          )}
        </SRow>
      ) : null}
      <SRow>
        <STitle numberOfLines={2}>
          {translate("booking.permission_edit_booking")}:
        </STitle>
        <SText>
          {toBoolean(resource?.data?.able_to_edit)
            ? translate("booking.booking_creator")
            : translate("booking.only_resource_manager")}
        </SText>
      </SRow>
      <SRow>
        <STitle numberOfLines={2}>
          {translate("booking.permission_cancel_booking")}:
        </STitle>
        <SText>
          {toBoolean(resource?.data?.able_to_cancel)
            ? translate("booking.booking_creator")
            : translate("booking.only_resource_manager")}
        </SText>
      </SRow>
      <SRow>
        <STitle>{translate("booking.approval_flow")}:</STitle>
        <SText>{getTextWorkflow(resource?.workflow)}</SText>
      </SRow>
    </SWrapper>
  );
});

const SWrapper = styled.View`
  background-color: ${(p) => p.theme.white};
  margin: 16px 16px 0 16px;
`;

const SRow = styled.View`
  flex-direction: row;
  border-bottom-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
  justify-content: space-between;
  align-items: center;
`;

const STitle = styled(UIText.BodyMedium400)`
  color: ${(p) => p.theme.neutral2};
  padding: 14px 0;
`;

const SText = styled(UIText.BodyMedium400)`
  color: ${(p) => p.theme.neutral1};
  margin-left: 8px;
`;

const SAvatarName = styled.View`
  flex-direction: row;
  align-items: center;
`;
