import { memo, useCallback } from "react";
import { styled, useTheme } from "@true/core";
import Animated, {
  interpolateColor,
  useAnimatedStyle,
  withTiming,
} from "react-native-reanimated";
import { UIButton, UITextStyle } from "@true/ui";
import { Icons } from "@true/asset";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";

export const SelectTypeBooking = memo(() => {
  const {
    heightScrollableTab,
    positionSelectType,
    showSelectType,
    selectedType,
  } = useBookingProvider();
  const theme = useTheme();

  const animatedStyle = useAnimatedStyle(() => ({
    top: heightScrollableTab.value - 4,
    left: positionSelectType.left.value,
    height: withTiming(showSelectType.value ? 60 : 0),
  }));

  const animatedTextStyle = useAnimatedStyle(() => ({
    color: interpolateColor(
      selectedType.value === "list" ? 1 : 0,
      [0, 1],
      [theme.neutral1, theme.blue1]
    ),
  }));

  const _animatedTextStyle = useAnimatedStyle(() => ({
    color: interpolateColor(
      selectedType.value === "schedule" ? 1 : 0,
      [0, 1],
      [theme.neutral1, theme.blue1]
    ),
  }));

  const animatedIconStyle = useAnimatedStyle(() => ({
    opacity: selectedType.value === "list" ? 1 : 0,
  }));

  const _animatedIconStyle = useAnimatedStyle(() => ({
    opacity: selectedType.value === "schedule" ? 1 : 0,
  }));

  const onPress = useCallback(() => {
    showSelectType.value = 0;
    selectedType.value = "list";
  }, []);

  const _onPress = useCallback(() => {
    showSelectType.value = 0;
    selectedType.value = "schedule";
  }, []);

  return (
    <SWrapper style={animatedStyle}>
      <SContainer>
        <SButton onPress={_onPress}>
          <SText style={[UITextStyle.caption_medium_400, _animatedTextStyle]}>
            Schedule
          </SText>
          <SIcon style={_animatedIconStyle} source={Icons.Filled.CHECK} />
        </SButton>
        <SDivider />
        <SButton onPress={onPress}>
          <SText style={[UITextStyle.caption_medium_400, animatedTextStyle]}>
            List
          </SText>
          <SIcon style={animatedIconStyle} source={Icons.Filled.CHECK} />
        </SButton>
      </SContainer>
    </SWrapper>
  );
});

const SContainer = styled.View`
  height: 60px;
  width: 89px;
  padding: 7px;
  border-radius: 4px;
  border: 1px solid ${(p) => p.theme.neutral4};
  overflow: scroll;
`;

const SWrapper = styled(Animated.View)`
  position: absolute;
  z-index: 9999;
  background-color: ${(p) => p.theme.neutral6};
  box-shadow: 0px 4px 16px rgba(223, 227, 236, 0.16);
  overflow: scroll;
`;

const SButton = styled(UIButton)`
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  flex-shrink: 1;
`;

const SIcon = styled(Animated.Image)`
  width: 12px;
  height: 12px;
  tint-color: ${(p) => p.theme.blue1};
`;

const SDivider = styled.View`
  background-color: ${(p) => p.theme.neutral5};
  width: 100%;
  height: 2px;
  margin: 6px 0;
`;

const SText = styled(Animated.Text)`
  text-align: left;
  flex-shrink: 1;
  width: 100%;
`;
