import { memo, useCallback } from "react";
import { styled } from "@true/core";
import { UIButton, UIText } from "@true/ui";
import { Icons } from "@true/asset";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";
import moment from "moment";
import { EFilterType } from "@/screens/CalendarScreen/type";
import { useResourceBookingProvider } from "@/screens/ResourceDetailScreen/provider";

export const SelectDayBooking = memo(() => {
  const { showCalendar, selectedDateString, onShowCalendar } =
    useResourceBookingProvider();

  // const { selectedDate, type } = useSelectedItems(selectedDateString);

  const onLeftPress = useCallback(() => {
    console.log("showCalendar", showCalendar.value);
    onShowCalendar();
  }, [onShowCalendar, showCalendar]);

  return (
    <SWrapper>
      <SLeftBtn onPress={onLeftPress}>
        <STileLeft>{moment().format("dddd")}</STileLeft>
        <SSubTitleBtn>{`, ${moment().format("DD MMM")}`}</SSubTitleBtn>
        <SIcon source={Icons.Outlined.CHEVRON_DOWN_SMALL} />
      </SLeftBtn>
      <SIconInfo source={Icons.Filled.INFORMATION_CIRCLE} />
    </SWrapper>
  );
});

const SWrapper = styled.View`
  padding: 8px 16px;
  background-color: ${(p) => p.theme.neutral6};
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  overflow: visible;
`;

const SLeftBtn = styled(UIButton)`
  flex-direction: row;
  align-items: center;
`;

const SSubTitleBtn = styled(UIText.BodyLarge400)`
  color: ${(p) => p.theme.neutral1};
  margin-right: 4px;
`;

const STileLeft = styled(UIText.HeaderSmall)`
  color: ${(p) => p.theme.neutral1};
`;

const SIcon = styled.Image`
  width: 16px;
  height: 16px;
  tint-color: ${(p) => p.theme.neutral2};
`;

const SIconInfo = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${(p) => p.theme.blue1};
`;
