import { memo, useCallback } from "react";
import { useBookingProvider } from "@/screens/CalendarScreen/provider";
import { styled, useTheme } from "@true/core";
import Animated, {
  interpolateColor,
  useAnimatedStyle,
  withTiming,
} from "react-native-reanimated";
import { UIButton, UITextStyle } from "@true/ui";
import { Icons } from "@true/asset";
import { capitalize } from "lodash";

const _Button = memo(
  ({ type, hideDivider }: { type: string; hideDivider?: boolean }) => {
    const theme = useTheme();
    const { showSelectStatus, selectedStatus } = useBookingProvider();
    const onPress = useCallback(() => {
      showSelectStatus.value = 0;
      selectedStatus.value = type;
    }, [type]);

    const animatedIconStyle = useAnimatedStyle(
      () => ({
        opacity: selectedStatus.value === type ? 1 : 0,
      }),
      [type]
    );

    const animatedTextStyle = useAnimatedStyle(() => ({
      color: interpolateColor(
        selectedStatus.value === type ? 1 : 0,
        [0, 1],
        [theme.neutral1, theme.blue1, type]
      ),
    }));

    return (
      <>
        <SButton onPress={onPress}>
          <SText style={[UITextStyle.caption_medium_400, animatedTextStyle]}>
            {capitalize(type || "all")}
          </SText>
          <SIcon style={animatedIconStyle} source={Icons.Filled.CHECK} />
        </SButton>
        {hideDivider ? null : <SDivider />}
      </>
    );
  }
);

export const SelectStatusBooking = memo(() => {
  const { heightScrollableTab, showSelectStatus } = useBookingProvider();

  const animatedStyle = useAnimatedStyle(() => ({
    top: heightScrollableTab.value - 4,
    height: withTiming(showSelectStatus.value ? 122 : 0),
  }));

  return (
    <SWrapper style={animatedStyle}>
      <SContainer>
        <_Button type={""} />
        <_Button type={"pending"} />
        <_Button type={"approved"} />
        <_Button type={"rejected"} />
      </SContainer>
    </SWrapper>
  );
});

const SContainer = styled.View`
  width: 89px;
  padding: 7px;
  border-radius: 4px;
  border: 1px solid ${(p) => p.theme.neutral4};
  overflow: scroll;
`;

const SWrapper = styled(Animated.View)`
  position: absolute;
  z-index: 9999;
  background-color: ${(p) => p.theme.neutral6};
  box-shadow: 0px 4px 16px rgba(223, 227, 236, 0.16);
  overflow: scroll;
  right: 16px;
`;

const SButton = styled(UIButton)`
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  flex-shrink: 1;
`;

const SIcon = styled(Animated.Image)`
  width: 12px;
  height: 12px;
  tint-color: ${(p) => p.theme.blue1};
`;

const SDivider = styled.View`
  background-color: ${(p) => p.theme.neutral5};
  width: 100%;
  height: 2px;
  margin: 6px 0;
`;

const SText = styled(Animated.Text)`
  text-align: left;
  flex-shrink: 1;
  width: 100%;
`;
