import React, { memo } from "react";
import { styled, translate } from "@true/core";
import { toBoolean, UIText } from "@true/ui";
import { useResources } from "@/store/resources";
import { EWorkflow } from "@/store/resources/type";

interface Props {
  id: string | number;
}

export const AdvancedSettings = memo(({ id }: Props) => {
  const resource = useResources(id);
  return (
    <SWrapper>
      <SRow>
        <STitle>{translate("booking.repeated_booking")}:</STitle>
        <SText>
          {toBoolean(resource?.data?.able_cycle)
            ? translate("booking.allow_repeated_booking")
            : translate("booking.not_allow_repeated_booking")}
        </SText>
      </SRow>
      <SRow>
        <STitle>{translate("booking.booking_limit")}:</STitle>
        <SText>
          {!toBoolean(resource?.data?.max_bookings)
            ? translate("booking.unlimited")
            : resource?.data?.max_bookings}
        </SText>
      </SRow>
      <SRow>
        <STitle>{translate("booking.require_for_approval")}:</STitle>
        <SText>
          <SText>
            {resource?.workflow === EWorkflow.AUTO_APPROVE
              ? translate("booking.no")
              : translate("booking.yes")}
          </SText>
        </SText>
      </SRow>
      <SRow>
        <STitle>{translate("booking.visibility_groups")}:</STitle>
        <SText>
          {resource?.teams && resource?.teams.length > 0
            ? resource?.teams
            : translate("booking.valid_all_company")}
        </SText>
      </SRow>
    </SWrapper>
  );
});

const SWrapper = styled.View`
  background-color: ${(p) => p.theme.white};
  margin: 16px 16px 0 16px;
`;

const SRow = styled.View`
  flex-direction: row;
  border-bottom-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
  justify-content: space-between;
  align-items: center;
`;

const STitle = styled(UIText.BodyMedium400)`
  color: ${(p) => p.theme.neutral2};
  padding: 14px 0;
`;

const SText = styled(UIText.BodyMedium400)`
  color: ${(p) => p.theme.neutral1};
  margin-left: 8px;
  text-align: right;
`;
