import React, { memo, useCallback, useRef } from "react";
import {
  getUser,
  goBack,
  styled,
  translate,
  useAsyncFn,
  useAutoToastError,
  useNavigationParams,
  useTheme,
} from "@true/core";
import {
  AnimatedBottomSpace,
  AnimatedKeyboardAvoidingViewRef,
  DynamicHeader,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UIInput,
  UIText,
} from "@true/ui";
import Animated, {
  interpolateColor,
  runOnUI,
  useAnimatedProps,
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";
import { Icons } from "@true/asset";
import { ScrollView, TouchableOpacity } from "react-native";
import { useBookings } from "@/store/booking";
import moment from "moment";
import { RepeatedBooking } from "@/screens/CreateBookingScreen/components/RepeatedBooking";
import {
  requestCreateBooking,
  requestEditBooking,
} from "@/store/booking/function";
import { BookingResource } from "@/screens/CreateBookingScreen/components/BookingResource";
import { navigateToBookingDetailScreen } from "@/utils/navigation";

const AnimatedTouchableOpacity =
  Animated.createAnimatedComponent<any>(TouchableOpacity);

export interface ICreateBookingmParams {
  name: string | number;
  "start-date": string | number;
  "start-time": string | number;
  "end-date": string | number;
  "end-time": string | number;
  content?: string;
  resource_id: number;
  resource_pick_booking_type?: string;
  followers: string;
  repeated_start_date: string;
  repeated_end_date: string;
  repeated_start_time: string;
  repeated_end_time: string;
  repeated_cycle_type: number;
  monthly_dates: string;
  direct_managers: string;
  weekly_dates: number[];
  // purpose?: string;
}

export interface CreateBookingScreen {
  bookingId?: string | number;
}

export const CreateBookingScreen = memo(() => {
  const { bookingId } = useNavigationParams<CreateBookingScreen>();
  const theme = useTheme();
  const isChangeText = useSharedValue(false);
  const offset = useSharedValue(100);
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useRef<any>(null);
  const booking = useBookings(bookingId);

  const params = useSharedValue<ICreateBookingmParams>({
    name: booking?.name ? booking?.name : "",
    "start-date": moment().unix(),
    "start-time": moment().unix(),
    "end-date": "",
    "end-time": "",
    content: booking?.content ? booking?.content : "",
    resource_id: booking?.resource_id ? booking?.resource_id : 0,
    resource_pick_booking_type: "single",
    followers: "",
    repeated_start_date: "",
    repeated_end_date: "",
    repeated_start_time: "",
    repeated_end_time: "",
    repeated_cycle_type: 107,
    monthly_dates: "",
    weekly_dates: [],
    direct_managers: "",
    // purpose: booking?.purpose || 0,
  });

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value: any) => {
        "worklet";

        if (_key === "name") {
          isChangeText.value = true;
        }

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [isChangeText, params]
  );

  const animatedProps = useAnimatedProps(() => {
    return {
      pointerEvents: !params.value["name"] ? "none" : "auto",
    };
  }) as any;

  const animatedButtonStyle = useAnimatedStyle(() => {
    const disabled = !params.value["name"] ? 1 : 0;
    return {
      backgroundColor: interpolateColor(
        disabled,
        [0, 1],
        [theme.primaryColor, theme.neutral2]
      ),
    };
  });

  // const animatedStyle = useAnimatedStyle(() => {
  //   return {
  //     maxHeight: withTiming(
  //         currentValue.value == value && value === 1 ? 500 : 0,
  //         { duration: 500 }
  //     ),
  //     overflow: "scroll",
  //   };
  // }, [value]);

  const [{ loading, error: errorCreate }, onSave] = useAsyncFn(async () => {
    if (bookingId) {
      await requestEditBooking(params.value);
      goBack();
    } else {
      const { booking } = await requestCreateBooking({
        ...params.value,
        "start-date": moment
          .unix(Number(params.value["start-date"]))
          .format("DD/MM/YYYY"),
        "start-time": moment
          .unix(Number(params.value["start-time"]))
          .format("HH:mm"),
        "end-date": moment
          .unix(Number(params.value["end-date"]))
          .format("DD/MM/YYYY"),
        "end-time": moment
          .unix(Number(params.value["end-time"]))
          .format("HH:mm"),
        repeated_start_date: moment
          .unix(Number(params.value["start-date"]))
          .format("DD/MM/YYYY"),
        repeated_start_time: moment
          .unix(Number(params.value["start-time"]))
          .format("HH:mm"),
        repeated_end_date: moment
          .unix(Number(params.value["end-date"]))
          .format("DD/MM/YYYY"),
        repeated_end_time: moment
          .unix(Number(params.value["end-time"]))
          .format("HH:mm"),
        followers:
          params.value.followers &&
          params.value.followers
            .map(
              (follower: string | number) => `@${getUser(follower)?.username}`
            )
            .join(" "),
      });
      navigateToBookingDetailScreen({ id: booking.id });
    }
  }, [bookingId]);

  useAutoToastError(errorCreate);

  return (
    <SScreenWrapper>
      <DynamicHeader
        title={
          0
            ? // bookingId
              translate("booking.edit_booking")
            : translate("booking.create_new_booking")
        }
        rightComponent={
          <Animated.View animatedProps={animatedProps}>
            <SSaveButton style={animatedButtonStyle} onPress={onSave}>
              <STitleButton>
                {0 ? translate("booking.save") : translate("booking.create")}
              </STitleButton>
            </SSaveButton>
          </Animated.View>
        }
      />
      <MyKeyboardAvoidingView
        ref={ref}
        scrollRef={scrollRef}
        scrollInputAdditionalOffset={offset}
      >
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={"handled"}>
          <SField>
            <SRow>
              <SIconHeader source={Icons.Filled.CLOCK} />
              <STextHeader>{translate("booking.time_resource")}</STextHeader>
            </SRow>
            <STextSubtitle>{translate("booking.booking_time")}</STextSubtitle>
            <SRow>
              <UIInput.DateTime
                keyName={"start-date"}
                params={params}
                title={translate("booking.start_date")}
                style={{ flex: 1, marginRight: 12 }}
                mode={"date"}
                onChangeValue={onChangeValue}
                required
              />
              <UIInput.DateTime
                keyName={"start-time"}
                params={params}
                title={translate("booking.start_time")}
                style={{ flex: 1 }}
                mode={"time"}
                onChangeValue={onChangeValue}
                required
              />
            </SRow>

            <SRow>
              <UIInput.DateTime
                keyName={"end-date"}
                params={params}
                title={translate("booking.end_date")}
                style={{ flex: 1, marginRight: 12 }}
                mode={"date"}
                onChangeValue={onChangeValue}
                required
              />
              <UIInput.DateTime
                keyName={"end-time"}
                params={params}
                title={translate("booking.end_time")}
                style={{ flex: 1 }}
                mode={"time"}
                onChangeValue={onChangeValue}
                required
              />
            </SRow>

            <RepeatedBooking params={params} />

            <STextSubtitle>
              {translate("booking.booking_resource")}
            </STextSubtitle>

            <BookingResource params={params} />
          </SField>
          <SField>
            <SRow>
              <SIconHeader source={Icons.Filled.INFORMATION_CIRCLE} />
              <STextHeader>
                {translate("booking.booking_information")}
              </STextHeader>
            </SRow>
            <UIInput.Text
              keyName={"name"}
              params={params}
              title={translate("booking.booking_title")}
              style={{ marginBottom: 12 }}
              onFocus={() => (isChangeText.value = true)}
              onSubmitEditing={() => (isChangeText.value = true)}
              onEndEditing={() => (isChangeText.value = false)}
              onBlur={() => (isChangeText.value = false)}
              onChangeValue={onChangeValue}
              required
            />
            <UIInput.UserPicker
              keyName={"followers"}
              type={"user"}
              multiple={true}
              params={params}
              title={translate("booking.followers")}
              style={{ marginBottom: 12 }}
              onChangeValue={onChangeValue}
            />
            <UIInput.Text
              keyName={"content"}
              params={params}
              title={translate("booking.description")}
              containerStyle={{ minHeight: 120, marginBottom: 12 }}
              onFocus={() => (isChangeText.value = true)}
              onSubmitEditing={() => (isChangeText.value = true)}
              onEndEditing={() => (isChangeText.value = false)}
              onBlur={() => (isChangeText.value = false)}
              onChangeValue={onChangeValue}
              multiline={true}
            />
          </SField>

          <AnimatedBottomSpace />
        </SScrollView>
      </MyKeyboardAvoidingView>
    </SScreenWrapper>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${(p) => p.theme.neutral8};
`;

const SScrollView = styled(ScrollView)``;

const SField = styled.View`
  margin-bottom: 8px;
  background-color: ${(p) => p.theme.white};
  padding: 12px 16px 0 16px;
`;

const SRow = styled.View`
  flex-direction: row;
  margin-bottom: 8px;
  align-items: center;
`;

const SIconHeader = styled.Image`
  height: 20px;
  width: 20px;
  tint-color: ${(p) => p.theme.primaryColor};
  margin-right: 8px;
`;

const STextHeader = styled(UIText.HeaderMedium)``;

const STextSubtitle = styled(UIText.BodyMedium500)`
  margin-bottom: 12px;
`;

const SSaveButton = styled(AnimatedTouchableOpacity)`
  padding: 4px 16px;
  background-color: ${(p) => p.theme.primaryColor};
  border-radius: 8px;
  margin-right: 16px;
  width: 90px;
  justify-content: center;
  align-items: center;
`;

const STitleButton = styled(UIText.ButtonMedium)`
  color: ${(p) => p.theme.white};
`;
