import _ from "lodash";
import React, { useMemo } from "react";
import { Pressable, StyleSheet, ViewStyle } from "react-native";
import Animated, {
  interpolateColor,
  SharedValue,
  useAnimatedStyle,
  useDerivedValue,
  useSharedValue,
  withSpring,
} from "react-native-reanimated";
import { getHeight, useTheme, styled } from "@true/core";

export const CustomSwitch = ({
  keyName,
  onPress,
  inActiveTrackColor = "#BFBFBF",
  disabledColor = "#BFBFBF",
  thumbColor = "#fff",
  animatedSwitch,
  disabled,
  scale = getHeight(16),
  debounce,
  ...props
}: {
  keyName: string;
  onPress: (keyName: string, value: boolean) => void;
  activeTrackColor?: string;
  inActiveTrackColor?: string;
  disabledColor?: string;
  thumbColor?: string;
  animatedSwitch: SharedValue<boolean>;
  disabled?: boolean;
  scale?: number;
  debounce?: boolean;
}) => {
  const theme = useTheme();
  const activeTrackColor = useMemo(
    () => props.activeTrackColor || theme.primaryColor,
    [props.activeTrackColor, theme.primaryColor]
  );
  const rightSwitch = useSharedValue(getRightSwitch(scale));
  const switchTranslate = useDerivedValue(() => {
    return withSpring(animatedSwitch.value ? rightSwitch.value : 0, {
      damping: 15,
      mass: 1,
      stiffness: 120,
      overshootClamping: false,
      restSpeedThreshold: 0.001,
      restDisplacementThreshold: 0.001,
    });
  }, [animatedSwitch, rightSwitch]);

  const memoizedOnSwitchPressCallback = React.useCallback(() => {
    onPress(keyName, !animatedSwitch.value);
  }, [onPress, animatedSwitch, keyName]);

  const onDebouncePress = React.useCallback(
    _.debounce(() => onPress && onPress(keyName, !animatedSwitch.value), 300),
    [onPress, animatedSwitch, keyName]
  );

  const animatedThumbStyle = useAnimatedStyle(() => {
    return {
      transform: [{ translateX: switchTranslate.value }],
    };
  }, [switchTranslate]);

  const interpolateBackgroundColor = useAnimatedStyle(() => {
    const backgroundColor = interpolateColor(
      switchTranslate.value,
      [0, rightSwitch.value],
      [inActiveTrackColor, disabled ? disabledColor : activeTrackColor]
    );

    return { backgroundColor };
  }, [rightSwitch, switchTranslate]);

  return (
    <SPressable
      disabled={disabled}
      onPress={debounce ? onDebouncePress : memoizedOnSwitchPressCallback}
    >
      <Animated.View
        style={[
          dynamicStyles.containerStyle(scale),
          interpolateBackgroundColor,
        ]}
      >
        <Animated.View
          style={[
            dynamicStyles.circleStyle(scale),
            { backgroundColor: thumbColor },
            animatedThumbStyle,
            styles.shadowValue,
          ]}
        />
      </Animated.View>
    </SPressable>
  );
};

const getSwitchWidth = (scale: number) => scale - 2 * getPadding(scale);
const getWidth = (scale: number) => (scale * 36) / 22;
const getPadding = (scale: number) => scale / 11;
const getRightSwitch = (scale: number) =>
  getWidth(scale) - getSwitchWidth(scale) - 2 * getPadding(scale);

const SPressable = styled(Pressable)`
  padding: 16px;
  margin: -16px;
`;

const dynamicStyles: Record<string, (value: any) => ViewStyle> = {
  circleStyle: (scale: number) => ({
    width: getSwitchWidth(scale),
    height: getSwitchWidth(scale),
    borderRadius: getSwitchWidth(scale),
  }),
  containerStyle: (scale: number) => ({
    width: getWidth(scale),
    paddingVertical: getPadding(scale),
    paddingHorizontal: getPadding(scale),
    borderRadius: scale,
  }),
};

const styles = StyleSheet.create({
  shadowValue: {
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.23,
    shadowRadius: 2.62,
    elevation: 4,
  },
});
