import React, { memo } from "react";
import { styled, useUser } from "@true/core";
import { Avatar, UIText } from "@true/ui";

interface Props {
  userId?: string | number;
}

export const FollowersCard = memo(({ userId }: Props) => {
  const followers = useUser(userId);

  return (
    <SWrapper>
      <SRow>
        <SAvatar userKey={userId || 0} size={40} />
        <SViewInfo>
          <STextName numberOfLines={1}>{followers?.name}</STextName>
          {followers?.title ? (
            <STextDesc numberOfLines={1}>{followers?.title}</STextDesc>
          ) : null}
        </SViewInfo>
      </SRow>
    </SWrapper>
  );
});

const SWrapper = styled.View`
  flex-direction: row;
  background-color: ${(p) => p.theme.white};
  margin-top: 16px;
  border-radius: 8px;
  align-content: center;
  flex: 1;
`;

const SRow = styled.View`
  flex-direction: row;
  flex: 1;
`;

const SAvatar = styled(Avatar)``;

const SViewInfo = styled.View`
  margin-left: 12px;
  align-items: flex-start;
  justify-content: center;
  flex: 1;
`;

const STextName = styled(UIText.BodyMedium500)`
  color: ${(p) => p.theme.neutral1};
  margin-bottom: 4px;
`;

const STextDesc = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
`;
