import React, { memo, PropsWithChildren, useCallback, useMemo } from "react";
import { EventBlock } from "@/screens/CalendarScreen/components/calendar/MyTimeLine/EventBlock";
import { PackedEvent } from "@/screens/CalendarScreen/type";
import { ScrollView, TextStyle, View, ViewStyle } from "react-native";
import { HOURS_TEXT_WIDTH } from "@/screens/CalendarScreen/components/calendar/MyTimeLine/TimeLineHours";

const EventsWrapper = memo(
  ({
    events,
    eventIndex,
    dayIndex,
    styles,
    format24h,
    renderEvent,
    onEventPress: _onEventPress,
  }: {
    events: PackedEvent[];
    eventIndex: number;
    dayIndex: number;
    styles: { [p: string]: ViewStyle | TextStyle };
    format24h: boolean;
    renderEvent?: (event: PackedEvent) => JSX.Element;
    onEventPress: (dateIndex: number, eventIndex: number) => void;
  }) => {
    const renderEvents = useCallback(
      (_top?: number) => {
        return (
          <>
            {events.map((event, j) => {
              eventIndex++;
              const onEventPress = () => _onEventPress(dayIndex, eventIndex);
              return (
                <EventBlock
                  key={event.id}
                  index={eventIndex}
                  event={{ ...event, top: event.top - (_top || 0) }}
                  styles={styles}
                  format24h={format24h}
                  onPress={onEventPress}
                  renderEvent={renderEvent}
                />
              );
            })}
          </>
        );
      },
      [eventIndex, styles, format24h, _onEventPress, renderEvent]
    );

    const { top, left, width, height } = useMemo(() => {
      let minTop = 9999;
      let maxTop = -1;

      let minLeft = 999;
      let maxLeft = -1;

      events.forEach((event) => {
        minTop = Math.min(minTop, event.top);
        maxTop = Math.max(maxTop, event.top + event.height);

        minLeft = Math.min(minLeft, event.left);
        maxLeft = Math.max(maxLeft, event.left + event.width + 8);
      });
      return {
        top: minTop,
        left: minLeft,
        width: maxLeft,
        height: maxTop - minTop,
      };
    }, [events]);

    if (events.length > 1) {
      return (
        <View
          style={{
            position: "absolute",
            top,
            left,
            overflow: "scroll",
          }}
        >
          <ScrollView
            horizontal={true}
            showsHorizontalScrollIndicator={false}
            style={{ flex: 1 }}
          >
            <View style={{ width, height }} />
            {renderEvents(top)}
          </ScrollView>
        </View>
      );
    }

    return <View>{renderEvents()}</View>;
  }
);

interface Props {
  dayIndex: number;
  calculatedEventGroup: any[][];
  onEventPress: (dateIndex: number, eventIndex: number) => void;
  renderEvent?: (event: PackedEvent) => JSX.Element;
  styles: { [p: string]: ViewStyle | TextStyle };
  format24h: boolean;
}

export const ListEvents = memo(
  ({
    dayIndex,
    calculatedEventGroup,
    onEventPress: _onEventPress,
    renderEvent,
    styles,
    format24h,
  }: Props) => {
    const events = useMemo(() => {
      if (!calculatedEventGroup || !calculatedEventGroup.length) return null;
      let eventIndex = -1;
      return calculatedEventGroup.map((_events, i) => (
        <EventsWrapper
          events={_events}
          key={i}
          dayIndex={dayIndex}
          eventIndex={eventIndex}
          format24h={format24h}
          styles={styles}
          onEventPress={_onEventPress}
          renderEvent={renderEvent}
        />
      ));
    }, [styles, format24h, _onEventPress, calculatedEventGroup, renderEvent]);

    return (
      <View
        pointerEvents={"box-none"}
        // @ts-ignore
        style={[
          {
            marginLeft: dayIndex === 0 ? HOURS_TEXT_WIDTH + 16 : undefined,
            overflow: "hidden",
          },
          styles.eventsContainer,
        ]}
      >
        {events}
      </View>
    );
  }
);
