import { memo, useCallback, useMemo } from "react";
import { Calendar } from "react-native-calendars";
import { styled, useTheme } from "@true/core";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";
import { UIButton, UIText, UITextStyle } from "@true/ui";
import { Icons } from "@true/asset";
import { LayoutChangeEvent, View } from "react-native";
import moment from "moment";
import { DayComponent } from "@/screens/CalendarScreen/components/calendar/MyCalendar/DayComponent";
import { getBookings, useBookingsByQuery } from "@/store/booking";
import { groupBy } from "lodash";
import ProgressBar from "@/components/ProgressBar";
import { DateData } from "react-native-calendars/src/types";
import { requestBookings } from "@/store/booking/function";
import { getDSEndOfTFromDS, getDSStartOfTFromDS } from "@/utils/calendar";
import { useResourceBookingProvider } from "@/screens/ResourceDetailScreen/provider";

export const MyResourceCalendar = memo(() => {
  const {
    heightScrollableTab,
    showCalendar,
    // selectedDateString,
    // currentTabIndex,
  } = useResourceBookingProvider();
  console.log("showCalendar", showCalendar.value);
  // const [tab, setTab] = useState("following");
  const fixHeight = useSharedValue(0);
  const ids = useBookingsByQuery("following");
  console.log("ids", ids);
  const theme = useTheme();

  const loading = useSharedValue(false);

  // console.log("selectedDateString.value", selectedDateString.value);

  const currentMonth = useSharedValue("10");

  const mapIds = useMemo(() => {
    return groupBy(
      ids.map((id) => ({
        id,
        dateString: moment(getBookings(id)?.etime || 0, "X")
          .startOf("days")
          .format("YYYY-MM-DD"),
      })),
      "dateString"
    );
  }, []);

  const fetchCalendar = useCallback(async (dateString: string) => {
    loading.value = true;
    const sMonth = getDSStartOfTFromDS("month", dateString);
    const eMonth = getDSEndOfTFromDS("month", dateString);
    await requestBookings({
      resource_id: "4",
      filter: "all",
      start: sMonth,
      end: eMonth,
    });
    loading.value = false;
  }, []);

  const onLayout = useCallback((e: LayoutChangeEvent) => {
    /* set _height, if _height == fixHeight then run animation,
     * else set new height to fix height */
    fixHeight.value = e.nativeEvent.layout.height;
  }, []);

  // console.log("heightScrollableTab", heightScrollableTab.value);

  const animatedStyle = useAnimatedStyle(() => ({
    top: heightScrollableTab.value,
    height: withTiming(showCalendar.value ? fixHeight.value : 0),
  }));

  const onPressArrowLeft = useCallback((substractMonth: any) => {
    substractMonth();
  }, []);

  const onPressArrowRight = useCallback((addMonth: any) => {
    addMonth();
  }, []);

  const onMonthChange = useCallback(
    async ({ dateString }: DateData) => {
      currentMonth.value = dateString;
      await fetchCalendar(dateString);
    },
    [fetchCalendar]
  );

  const renderArrow = useCallback((direction: any) => {
    if (direction === "left") {
      return <SHeaderBtn source={Icons.Outlined.CHEVRON_LEFT} disabled />;
    }
    return <SHeaderBtn source={Icons.Outlined.CHEVRON_RIGHT} disabled />;
  }, []);

  const renderHeader = useCallback((month: any) => {
    return (
      <STitleHeader>{moment(new Date(month)).format("MMM YYYY")}</STitleHeader>
    );
  }, []);

  // const dayComponent = useCallback(
  //   (dayProps: any) => {
  //     const { date, state } = dayProps;
  //     const { dateString, day } = date;
  //     const dayData = mapIds[dateString];
  //     return <DayComponent dateString={dateString} day={day} data={dayData} />;
  //   },
  //   [mapIds]
  // );

  const animatedLoadingStyle = useAnimatedStyle(() => ({
    opacity: withTiming(loading.value ? 1 : 0),
  }));

  return (
    <SCalendarWrapper style={animatedStyle}>
      <ViewLoading style={animatedLoadingStyle}>
        <ProgressBar
          indeterminate
          backgroundColor={theme.primaryColor}
          trackColor={"transparent"}
        />
      </ViewLoading>
      <View style={{ overflow: "scroll" }} onLayout={onLayout}>
        <SCalendar
          firstDay={1}
          enableSwipeMonths={true}
          hideExtraDays={true}
          renderHeader={renderHeader}
          renderArrow={renderArrow}
          onPressArrowLeft={onPressArrowLeft}
          onPressArrowRight={onPressArrowRight}
          // dayComponent={dayComponent}
          onMonthChange={onMonthChange}
        />
      </View>
    </SCalendarWrapper>
  );
});

const ViewLoading = styled(Animated.View)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999;
`;

const SCalendarWrapper = styled(Animated.View)`
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  overflow: scroll;
  background-color: ${(p) => p.theme.neutral8};
`;

const SHeaderBtn = styled(UIButton).attrs((p) => ({
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral1,
  },
}))`
  height: 32px;
  width: 32px;
  background-color: ${(p) => p.theme.neutral7};
  border-radius: 100px;
  border: 1px solid ${(p) => p.theme.neutral6}
  padding: 8px;
  align-items: center;
  justify-content: center;
`;

const SCalendar = styled(Calendar).attrs((p) => ({
  theme: {
    "stylesheet.calendar.header": {
      week: {
        flexDirection: "row",
        width: "100%",
        justifyContent: "space-around",
        height: 32,
        alignItems: "center",
      },
      header: {
        backgroundColor: p.theme.neutral8,
        alignItems: "center",
        flexDirection: "row",
        paddingHorizontal: 12,
        paddingVertical: 8,
        justifyContent: "space-between",
        borderBottomWidth: 1,
        borderColor: p.theme.neutral5,
      },
      dayHeader: {
        ...UITextStyle.caption_medium_500,
        color: p.theme.neutral2,
      },
      arrow: {
        padding: 0,
      },
    },
    "stylesheet.calendar.main": {
      container: {
        paddingLeft: 0,
        paddingRight: 0,
        width: "100%",
      },
      monthView: {
        backgroundColor: p.theme.neutral8,
        width: "100%",
      },
    },
  },
}))`
  background-color: ${(p) => p.theme.neutral8};
  height: 100%;
  width: 100%;
`;

const STitleHeader = styled(UIText.BodyLarge400)`
  color: ${(p) => p.theme.neutral1};
  text-align: center;
`;
