import moment from "moment";

export const getDSStartOfTFromDS = (
  unit: moment.unitOfTime.StartOf,
  ds?: string
) => {
  return moment(ds).startOf(unit).format("YYYY-MM-DD");
};

export const getDSEndOfTFromDS = (
  unit: moment.unitOfTime.StartOf,
  ds?: string
) => {
  return moment(ds).endOf(unit).format("YYYY-MM-DD");
};
