import React, { memo } from "react";
import { styled, translate } from "@true/core";
import { UIText } from "@true/ui";
import { useResources } from "@/store/resources";
import FastImage from "react-native-fast-image";
import { useResourceGroups } from "@/store/groups";

interface Props {
  resourceId?: string | number;
}

export const ResourceCard = memo(({ resourceId }: Props) => {
  const resource = useResources(resourceId);
  const group = useResourceGroups(resource?.group_id);

  return (
    <SWrapper>
      <SRow>
        <SImage source={{ uri: resource?.thumbnail }} />
        <SViewInfo>
          <SViewTag>
            <STextTag>{group?.name}</STextTag>
          </SViewTag>
          <STextName numberOfLines={1}>{resource?.name}</STextName>
          <STextDesc numberOfLines={1}>
            {resource?.content
              ? resource?.content
              : translate("booking.no_description")}
          </STextDesc>
        </SViewInfo>
      </SRow>
    </SWrapper>
  );
});

const SWrapper = styled.View`
  flex-direction: row;
  background-color: ${(p) => p.theme.white};
  padding: 12px;
  margin-top: 12px;
  border-radius: 8px;
  align-content: center;
  border-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

const SRow = styled.View`
  flex-direction: row;
`;

const SImage = styled(FastImage)`
  height: 76px;
  width: 120px;
  border-radius: 8px;
`;

const SViewInfo = styled.View`
  margin-left: 12px;
  align-items: flex-start;
  justify-content: center;
`;

const SViewTag = styled.View`
  border-radius: 80px;
  background-color: ${(p) => p.theme.neutral8};
  margin-bottom: 4px;
`;

const STextTag = styled(UIText.CaptionSmall400)`
  color: ${(p) => p.theme.neutral2};
  padding: 0 6px;
`;

const STextName = styled(UIText.HeaderSmall)`
  color: ${(p) => p.theme.neutral1};
  margin-bottom: 4px;
`;

const STextDesc = styled(UIText.CaptionMedium400)`
  color: ${(p) => p.theme.neutral2};
`;
