import {
  ECycleType,
  EStatusBooking,
  IBooking,
  IRepeatedBooking,
} from "@/store/booking/type";
import moment from "moment/moment";
import { keyBy } from "lodash";

export const getAllBookingInThisMonth = (
  sMonthString: string,
  eMonthString: string,
  bookings?: IBooking[],
  repeated_bookings?: IRepeatedBooking[]
) => {
  let list_bookings: any[] = [...(bookings || [])];

  // map booking to eliminate approved or duplicate bookings when calculating to get weekly or monthly repeat bookings
  let mapBookings: any = keyBy(
    list_bookings,
    (booking) => booking.stime + booking.repeated_booking_id.toString()
  );

  if (repeated_bookings && repeated_bookings.length > 0) {
    repeated_bookings.forEach((booking) => {
      if (booking.days && booking.days.length) {
        if (booking.cycle_type == ECycleType.WEEK) {
          const _list = getRepeatBookingsWeekly(
            booking,
            sMonthString,
            eMonthString,
            mapBookings
          );
          list_bookings = list_bookings.concat(_list);
        }

        if (booking.cycle_type == ECycleType.MONTH) {
          const _list = getRepeatBookingsMonthly(
            booking,
            sMonthString,
            eMonthString,
            mapBookings
          );
          list_bookings = list_bookings.concat(_list);
        }
      }
    });
  }

  return list_bookings.sort((a, b) => sortHandle(a, b));
};

const getRepeatBookingsMonthly = (
  booking: IRepeatedBooking,
  sMonthString: string,
  eMonthString: string,
  mapBookings: any
) => {
  const sMonth = moment(sMonthString).startOf("days");
  const eMonth = moment(eMonthString).startOf("days");
  let list: IRepeatedBooking[] = [];

  // when day end of repeated booking = 0 => repeat infinitely
  if (
    (sMonth.unix() < booking.dayend || !Number(booking.dayend)) &&
    eMonth.unix() > booking.daystart
  ) {
  }
  const sDayOfMonth = moment(
    Math.max(sMonth.unix(), booking.daystart),
    "X"
  ).date();
  const eDayOfMonth = moment(
    !Number(booking.dayend)
      ? eMonth.unix()
      : Math.min(eMonth.unix(), booking.dayend),
    "X"
  ).date();

  booking.days.map((day) => {
    if (parseInt(day) <= eDayOfMonth && parseInt(day) >= sDayOfMonth) {
      const tsDateOfMonth = moment(sMonth, "X")
        .date(parseInt(day))
        .startOf("days")
        .unix();
      const _stime = tsDateOfMonth + booking.timestart;
      if (!mapBookings[_stime + booking.id.toString()]) {
        list.push({
          ...booking,
          stime: _stime,
          etime: tsDateOfMonth + booking.timeend,
          id: (tsDateOfMonth + booking.timestart).toString(),
          status: EStatusBooking.PENDING,
        });
      }
    }
  });

  return list;
};

const getRepeatBookingsWeekly = (
  booking: IRepeatedBooking,
  sMonthString: string,
  eMonthString: string,
  mapBookings: any
) => {
  const sMonth = moment(sMonthString).startOf("days");
  const eMonth = moment(eMonthString).startOf("days");
  let list: IRepeatedBooking[] = [];

  // when day end of repeated booking = 0 => repeat infinitely
  if (
    (sMonth.unix() < booking.dayend || !Number(booking.dayend)) &&
    eMonth.unix() > booking.daystart
  ) {
    const start = Math.max(sMonth.unix(), booking.daystart);
    const end = !Number(booking.dayend)
      ? eMonth.unix()
      : Math.min(eMonth.unix(), booking.dayend);

    const numberOfWeeks = moment(end, "X").diff(moment(start, "X"), "weeks");

    booking.days.forEach((day) => {
      const dayOfWeek = handleDayOfWeekRepeatedBooking(day);
      let i = 1;
      if (start <= moment(start, "X").days(dayOfWeek).startOf("days").unix()) {
        i--;
      }
      while (i <= numberOfWeeks) {
        const _day = moment(start, "X")
          .day(i * 7 + dayOfWeek)
          .startOf("days")
          .unix();
        const _stime = _day + booking.timestart;
        if (!mapBookings[_stime + booking.id.toString()]) {
          const _booking = {
            ...booking,
            stime: _stime,
            etime: _day + booking.timeend,
            id: (_day + booking.timestart).toString(),
            status: EStatusBooking.PENDING,
          };
          list.push(_booking);
          mapBookings = {
            ...mapBookings,
            [_stime + booking.id.toString()]: _booking,
          };
        }
        i++;
      }

      if (end >= moment(end, "X").days(dayOfWeek).startOf("days").unix()) {
        const tsDayOfEndWeek = moment(end, "X")
          .days(dayOfWeek)
          .startOf("days")
          .unix();
        const _stime = tsDayOfEndWeek + booking.timestart;
        if (!mapBookings[_stime + booking.id.toString()]) {
          list.push({
            ...booking,
            stime: _stime,
            etime: tsDayOfEndWeek + booking.timeend,
            id: (tsDayOfEndWeek + booking.timestart).toString(),
            status: EStatusBooking.PENDING,
          });
        }
      }
    });
  }

  return list;
};

const handleDayOfWeekRepeatedBooking = (day: string) => {
  if (parseInt(day) === 6) return 0;

  return parseInt(day) + 1;
};

const sortHandle = (
  a?: IBooking | IRepeatedBooking,
  b?: IBooking | IRepeatedBooking
) => {
  if (!a || !b) {
    return 0;
  }
  const start_time_a = a?.stime || 0;
  const start_time_b = b?.stime || 0;
  return start_time_a - start_time_b;
};
